@use "variables" as v;
@use "mixins" as m;

html {
  font-size: 62.5%;
  box-sizing: border-box;
}
*,
*:before,
*:after {
  box-sizing: inherit;
}

body {
  font-family: v.$fuente_principal;
  font-size: 1.6rem;
}
p {
  color: v.$negro;
  font-size: 2rem;
  line-height: 2;
}
.contenedor {
  width: 95%;
  max-width: 1200px;
  margin: 0 auto;
}

.seccion {
  padding: 5rem 0;
  @include m.tablet {
    padding: 8rem;
  }
}

a {
  text-decoration: none;
}

img {
  max-width: 100%;
  display: block;
}

h1,
h2,
h3 {
  margin: 0 0 2rem 0;
}
h1 {
  font-size: 4rem;
}

h2 {
  color: v.$secundario;
  font-size: 3.6rem;
  display: inline-block;
  margin-top: 5rem;

  @include m.tablet {
    margin-top: 0;
  }

  &::after {
    content: "";
    display: block;
    height: 1rem;
    width: 103%;
    background-color: v.$principal;
    margin: -1.4rem 0 0 0;
  }
}

h3 {
  font-size: 2.8rem;
  text-align: center;
  text-transform: uppercase;
}
ul {
  padding: 0;
  list-style: none;
}
